<template>
  <div class="toolbar">
    <el-button @click="handleClickRefresh" title="刷新">
      <el-icon><Refresh /></el-icon>
    </el-button>

    <el-button @click="handleClickAdd" title="添加本地仓库">
      <el-icon><Plus /></el-icon>
    </el-button>

    <MyInsertModeDialog v-model="displayInsertModeDialog" />
  </div>
</template>

<script setup>
import { Refresh, Plus } from "@element-plus/icons-vue";
</script>

<script>
import MyInsertModeDialog from "./insert-repo-mode-dialog.vue";

export default {
  name: "settings-local-repositories-toolbar",

  components: { MyInsertModeDialog },

  data() {
    return {
      displayInsertModeDialog: false,
    };
  },

  methods: {
    fetch() {
      this.$store.dispatch("local_repository/fetch", {});
    },

    handleClickAdd() {
      this.displayInsertModeDialog = true;
    },

    handleClickRefresh() {
      this.fetch();
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>
  
<style scoped>
.toolbar {
  display: flex;
  flex-direction: row;
  justify-content: right;
}
</style>
