<template>
  <div>
    <el-table :data="items">
      <el-table-column label="名称" prop="name" sortable ></el-table-column>
      <el-table-column label="路径" prop="path" sortable ></el-table-column>
      <el-table-column label="状态" prop="state" sortable ></el-table-column>
      <el-table-column label="操作">
        <template #default="scope">
          <div class="operation-buttons">
            <el-button
              type="primary"
              link
              @click="handleClickItemDetail(scope.row)"
              >详情</el-button
            >
            <el-button
              type="primary"
              link
              @click="handleClickEditItem(scope.row)"
              >编辑</el-button
            >
            <el-button
              type="danger"
              link
              @click="handleClickRemoveItem(scope.row)"
              >移除</el-button
            >
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import { ElMessageBox } from "element-plus";

export default {
  name: "settings-local-repositories-table",

  computed: {
    items() {
      return this.$store.getters["local_repository/currentPageItems"];
    },
  },

  methods: {
    handleClickEditItem(item) {
      let name = "edit";
      let e = { name, item };
      this.$emit("action", e);
    },

    handleClickItemDetail(item) {
      let name = "detail";
      let e = { name, item };
      this.$emit("action", e);
    },

    handleClickRemoveItem(item) {
      let title = "移除仓库";
      let text = "确定要移除 [" + item.name + "] 这个仓库吗？";
      let desc =
        "该操作不会把磁盘上的仓库数据删除，仅将对它的引用从WPM数据库中移除。";
      ElMessageBox.confirm(text + desc, title, {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.removeItem(item);
        })
        .catch(() => {});
    },

    removeItem(item) {
      this.$store.dispatch("local_repository/remove", item);
    },
  },
};
</script>

<style scoped>
.operation-buttons {
  filter: opacity(1);
}

.operation-buttons:hover {
  filter: opacity(1);
}
</style>
