<template>
  <el-dialog
    title="修改仓库"
    :modelValue="modelValue"
    @update:modelValue="updateModelValue"
  >
    <el-form label-width="120px">
      <el-form-item label="名称">
        <el-input v-model="item1.name"></el-input>
      </el-form-item>
      <el-form-item label="路径">
        <el-input v-model="item1.path"></el-input>
      </el-form-item>
      <el-form-item label="路径（工作区）">
        {{ item1.workspace_path }}
      </el-form-item>
      <el-form-item label="路径（库）">
        {{ item1.repository_path }}
      </el-form-item>
      <el-form-item label="描述">
        <el-input type="textarea" v-model="item1.description"></el-input>
      </el-form-item>
    </el-form>

    <div>
      <el-button type="success" @click="handleClickSave">保存</el-button>
      <el-button type="default" @click="handleClickCancel">取消</el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: "local-repo-item-edit",

  data() {
    return {
      item1: {},
    };
  },

  methods: {
    updateModelValue(value) {
      this.$emit("update:modelValue", value);
    },

    handleClickSave() {
      let item = this.item1;
      this.$store.dispatch("local_repository/update", item).then(() => {
        this.close();
      });
    },

    handleClickCancel() {
      this.close();
    },

    close() {
      this.updateModelValue(false);
    },
  },

  props: {
    modelValue: Boolean,
    item: Object,
  },

  watch: {
    item(value) {
      let str = JSON.stringify(value);
      this.item1 = JSON.parse(str);
    },
  },
};
</script>
