<template>
  <div class="settings-index-page">
    <h1>本地仓库</h1>

    <MyToolbar />

    <MySearch class="search-box" />

    <MyPager class="pager-box" />

    <MyTable @action="handleAction" />

    <MyEdit v-model="displayEditDialog" :item="theEditItem" />
  </div>
</template>
  
<script>
import MyTable from "./table.vue";
import MyPager from "./pager.vue";
import MySearch from "./search-box.vue";
import MyToolbar from "./toolbar.vue";
import MyEdit from "./item-edit.vue";

export default {
  name: "settings-local-repositories",

  components: {
    MyTable,
    MyPager,
    MySearch,
    MyToolbar,
    MyEdit,
  },

  data() {
    return {
      displayEditDialog: false,
      theEditItem: {},
    };
  },

  methods: {
    handleAction(e) {
      let name = e.name;
      if (name == "edit") {
        this.editItem(e.item);
      } else if (name == "detail") {
        this.openItemDetail(e.item);
      } else {
        console.warn("unsupported action:" + name);
      }
    },

    editItem(item) {
      this.theEditItem = item;
      this.displayEditDialog = true;
    },

    openItemDetail(item) {
      this.nop(item);
    },

    nop() {},
  },
};
</script>

<style scoped>
.settings-index-page {
  text-align: left;
}

.search-box {
  max-width: 300px;
}

.pager-box {
  margin-top: 50px;
  margin-bottom: 20px;
}
</style>
